<template>
  <div>
    <div class="modal-content" v-show="!formSentSuccessfully">
      <h4>Suggest a solution to a problem encountered</h4>
      <p>
        In order to improve our service, give us your contribution by sharing
        and describing your experience.
      </p>
      <div class="container">
        <div class="row">
          <div class="col s12" id="new-issue-form">
            <div class="row">
              <div class="input-field col s12">
                <textarea
                  v-model="problemText"
                  id="textarea1"
                  name="issue"
                  class="materialize-textarea"
                ></textarea>
                <label for="textarea1"
                  >Describe the problem in a few lines</label
                >
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <textarea
                  v-model="solutionText"
                  id="textarea2"
                  name="solution"
                  class="materialize-textarea"
                ></textarea>
                <label for="textarea2"
                  >Describe your solution in a few lines</label
                >
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <label>
                  <input type="checkbox" v-model="addReference" name="addRef" />
                  <span>Add a conversation's reference</span>
                </label>
              </div>
            </div>
            <div class="row" v-show="addReference">
              <div class="col s12">
                <select v-model="conversationIdSelected">
                  <option value="" disabled selected>Choose your option</option>
                  <option
                    v-for="(option, index) in options"
                    :key="index"
                    :value="option.value"
                  >
                    Conversation number {{ options.length - index }}
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-content" v-show="formSentSuccessfully">
      <div class="card teal">
        <div class="card-content white-text">
          <span class="card-title"
            >Thank you ! we received successfully your data</span
          >
          <p>Thank you for helping us to improve this chatbot service.</p>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <button
        type="submit"
        class="btn waves-effect waves-green"
        :class="buttonClassComputed"
        @click="sendFormData"
        v-show="!formSentSuccessfully"
      >
        Send
      </button>

      <a
        href="#!"
        @click="resetData"
        class="modal-close waves-effect waves-green btn-flat modal-link-click-exception"
        >Close</a
      >
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "SolutionForm",

  data: function() {
    return {
      problemText: "",
      solutionText: "",
      conversationIdSelected: "",
      addReference: false,
      options: [],
      formSentSuccessfully: false
    };
  },
  computed: {
    buttonClassComputed() {
      return this.problemText !== "" && this.solutionText !== ""
        ? ""
        : "disabled";
    }
  },

  methods: {
    resetData() {
      this.problemText = "";
      this.solutionText = "";
      this.conversationIdSelected = "";
      this.addReference = false;
      this.options = [];
      this.formSentSuccessfully = false;
    },
    sendFormData(userTextMessage) {
      axios
        .post(
          "/user/new-solution",
          {
            problem_text: this.problemText,
            solution_text: this.solutionText,
            add_reference: this.addReference,
            conversation_id: this.conversationIdSelected
          },
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json"
            }
          }
        )
        .then(response => {
          if (response.data !== null && response.data === "success") {
            this.formSentSuccessfully = true;
          }
        })
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    },

    getConversationIds() {
      axios
        .get("/user/history/ids")
        .then(response => {
          if (response.data !== null) {
            this.options = response.data.conversations_ids.map(el => {
              return {
                value: el
              };
            });
          }
        })
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    }
  },
  watch: {
    addReference(val, oldVal) {
      if (val === true && this.options.length === 0) {
        this.getConversationIds();
      }
    }
  }
};
</script>

<style lang="scss">
@import "/static/materialize/css/materialize.css";
@import "/static/css/style.css";
@import "/static/css/chatbot.css";
</style>
